import React from 'react'
import Image from '../img/img1.png'
import { KeyboardArrowDown } from "@material-ui/icons";

function About() {
  return (
    <div className="container">
      <div className="box">
        <div className="img">
        <img src={Image}/>
        </div>
        <p className="text"></p>
        <p className="subtext"> <br></br><br></br>
          !<br></br>
          !</p>
        <div className="button"><KeyboardArrowDown></KeyboardArrowDown> </div>
        </div>
    </div>
  )
}

export default About